import M from 'materialize-css';
import { Select } from 'react-materialize';

let months = ['', '', '', '', '', '', '', '', '', '', '', ''];

function MonthPicker({date, setDate}) {
    function changeHandler(event) {
        switch (event.target.value) {
            case '': setDate(new Date(date.getFullYear(), 0));
            break;
            case '': setDate(new Date(date.getFullYear(), 1));
            break;
            case '': setDate(new Date(date.getFullYear(), 2));
            break;
            case '': setDate(new Date(date.getFullYear(), 3));
            break;
            case '': setDate(new Date(date.getFullYear(), 4));
            break;
            case '': setDate(new Date(date.getFullYear(), 5));
            break;
            case '': setDate(new Date(date.getFullYear(), 6));
            break;
            case '': setDate(new Date(date.getFullYear(), 7));
            break;
            case '': setDate(new Date(date.getFullYear(), 8));
            break;
            case '': setDate(new Date(date.getFullYear(), 9));
            break;
            case '': setDate(new Date(date.getFullYear(), 10));
            break;
            case '': setDate(new Date(date.getFullYear(), 11));
            break;
            default: setDate(null);
        }
    } 

    const result = [];

    for (let i = 0; i <= 11; i++) {
        let opt = <option key={i}>{months[i]}</option>;
        result.push(opt);
    }

    // return <select onChange={event => changeHandler(event)} defaultValue={months[date.getMonth()]}>
    //     {result}
    // </select>

    return (
        <Select
            id="Select-33"
            multiple={false}
            onChange={changeHandler} 
            options={{
                classes: '',
                dropdownOptions: {
                alignment: 'left',
                autoTrigger: true,
                closeOnClick: true,
                constrainWidth: true,
                coverTrigger: true,
                hover: false,
                inDuration: 150,
                onCloseEnd: null,
                onCloseStart: null,
                onOpenEnd: null,
                onOpenStart: null,
                outDuration: 250
                }
            }}
            defaultValue={months[date.getMonth()]}
         >
             <option disabled> </option>
            {result}
        </Select>
    )
}

export default MonthPicker;